/* stylelint-disable selector-no-qualifying-type */

//.custom-form class required on any Custom Form Asset in Matrix to inherit these styles
.custom-form {
    @include pnp-print-hide;

    padding: 1rem;
    input[type='number'],
    input[type='text'],
    input[type='search'],
    input[type='email'],
    textarea,
    select {
        @include pnp-typeface(text-18);
        width: 100%;
        max-width: 404px;
        padding: 10px;
        color: $white;
        background: transparent;
        border: 1px solid $white;
        @include pnp-media-breakpoint-up(md) {
            width: 404px;
        }

        &:focus {
            outline: 0;
            box-shadow: 0 0 4px $primary-a;
        }
    }

    select:not([multiple]) {
        @include pnp-custom-select;
        background-color: transparent;
    }

    select[multiple] {
        @include pnp-typeface(text-18);
        color: $white;
    }

    input[type='checkbox'] {
        @include pnp-custom-checkbox;
    }
    input[type='radio'] {
        @include pnp-custom-radio-button;
    }

    label {
        @include pnp-typeface(text-18-bold);
    }

    ::placeholder {
        @include pnp-typeface(text-18);
        color: $white;
    }

    //SQ-Form specific
    .sq-form-question-datetime {
        .sq-form-question-answer {
            select {
                width: initial;
                margin-right: 10px;
                margin-bottom: 10px;
                margin-left: 10px;
            }
        }
    }

    .sq-form-question-answer {
        ul {
            @include pnp-list-reset;
            li {
                margin-bottom: 0;
            }
        }
    }

    .sq-form-error {
        display: none;
    }

    .sq-form-question-title {
        @include pnp-typeface(text-16-bold);
        display: block;
    }

    .sq-form-question {
        margin-top: 32px;
    }

    .sq-form-question-note,
    .sq-form-required-field-note {
        @include pnp-typeface(text-14);
        display: block;
    }

    .sq-form-question.sq-form-question-error {
        input,
        textarea,
        select {
            color: $utility-alert;
            box-shadow: 0 0 4px rgba(200, 53, 50, 0.4), 0 0 4px $utility-alert;
        }
        .sq-form-error {
            @include pnp-typeface(text-14);
            display: block;
            margin-top: 3px;
            color: $utility-alert;
            line-height: 18px;
        }
    }

    .sq-form-section {
        margin: 1rem 0;
        padding: 1rem;
        border: 1px solid $black;
        .sq-form-question {
            margin-top: 0;
        }
    }

    .sq-form-section-title {
        @include pnp-typeface(text-24, no-margin);
        margin-top: 1rem;
    }

    button,
    .sq-form-submit,
    ::file-selector-button {
        @include pnp-button;
    }

    input[type='file'] {
        &:focus {
            outline: 0;
            box-shadow: 0 0 4px $primary-a;
        }
    }

    /*
        Auto generate background colours based on colour maps. Resolves to:
        &-black {
            background: #000;
        }
    */
    &--bg {
        @include pnp-generate-background-for-colour-map($pnp-primary-colours);
        @include pnp-generate-background-for-colour-map($pnp-neutral-colours);
    }

    /*
        Auto generate text colours based on colour maps. Resolves to:
        &-black {
            color: #000;
        }
    */
    &--color {
        @include pnp-generate-foreground-for-colour-map($pnp-primary-colours);
        @include pnp-generate-foreground-for-colour-map($pnp-neutral-colours);
    }

    &--color-black,
    &--color-grey70 {
        .sq-form-question-note,
        .sq-form-required-field-note {
            color: $grey-70;
        }
        input[type='number'],
        input[type='text'],
        input[type='search'],
        input[type='email'],
        textarea,
        select {
            color: $black;
            border-color: $grey-80;
        }
        ::placeholder {
            color: $grey-70;
        }
        select[multiple] {
            color: $black;
        }
    }

    &--color-white {
        padding: 0;
        input[type='checkbox'],
        input[type='radio'] {
            &:disabled + label {
                color: rgba($white, 0.12);
            }
        }
        .sq-form-question.sq-form-question-error {
            .sq-form-error {
                color: $white;
                text-decoration: underline;
            }
        }
    }

    &--bg-white {
        input[type='number'],
        input[type='text'],
        input[type='search'],
        input[type='email'],
        textarea,
        select {
            &:disabled {
                background: $grey-10;
            }
        }
    }

    &--bg-primary-a {
        input[type='number'],
        input[type='text'],
        input[type='search'],
        input[type='email'],
        textarea,
        select {
            &:disabled {
                background-color: rgba($white, 0.12);
            }
        }

        button,
        .sq-form-submit,
        ::file-selector-button {
            color: $primary-a;
            background-color: $white;
        }
    }

    // Make sure parent position is relative.
    .input-with-reset {
        @include pnp-reset-button;
        position: absolute;
        top: calc(50% - 1.3rem);
        right: 60px;
        display: none;
        color: $primary-a;
        background-color: $white;
        cursor: pointer;
    }
}
