@use 'sass:math';

.mega-menu-wrapper {
    width: 100vw;
    margin-top: 5px;
    background: $grey-10;

    // See below for style variations
}

.mega-menu {
    @include pnp-svg-icon-classes;

    display: flex;
    max-width: 1400px;
    margin: auto;

    &__image-wrapper {
        display: flex;
        flex-shrink: 0;
        align-items: flex-start;
        width: 300px;
        margin-right: 2rem;
    }

    &__image {
        width: 100%;
        height: auto;
        max-height: 300px;
        object-fit: contain;
    }

    &__content {
        flex-shrink: 0;
        width: 300px;
        margin-right: 2rem;
    }

    &__title {
        @include pnp-typeface(heading-32);
    }

    &__description {
        @include pnp-typeface(text-16);
    }

    &__cta {
        @include pnp-button;
        @include pnp-typeface(text-16, no-margin);
        display: inline-flex;
        align-items: center;
        margin-top: 2rem;

        &[href=''] {
            display: none;
        }

        .svg-icon {
            margin-left: 0.5rem;
        }
    }

    &__nav-list {
        @include pnp-list-reset;
    }

    &__nav-item {
        color: $primary-a;
    }

    &__nav-link {
        @include pnp-reset-button();
        @include pnp-typeface(text-16, no-margin);

        display: block;
        color: $primary-a;
        text-decoration: none;

        &:focus {
            outline: solid 2px $primary-a;
            outline-offset: -2px;
        }
    }
}

// Standard single teir version
.mega-menu-wrapper--single-teir .mega-menu {
    padding: 2.5rem 0;

    &__nav-list {
        column-count: 3;
        column-fill: balance;
    }

    &__nav-item {
        position: relative;
        break-inside: avoid;

        .svg-icon {
            position: absolute;
            top: 23px;
            left: 0;
        }
    }

    &__nav-link {
        padding: 20px;
        padding-left: 28px;

        &:hover,
        &:focus {
            text-decoration: underline;
        }
    }
}

// Double teir menu variation
.mega-menu-wrapper--double-teir {
    overflow: hidden;

    .mega-menu__nav-list--level-1 {
        position: relative;
        width: math.div($max-width, 6);
        min-height: var(--subNavHeightNeeded);
        background: $primary-a-light;
        transition: min-height 0.25s 0s linear;

        @include pnp-media-prefers-reduced-motion {
            transition: none;
            animation: none;
        }

        > .mega-menu__nav-item {
            padding: 0 0.5rem 0 3rem;

            &:hover,
            &--active,
            &:focus {
                background: $grey-10;

                .mega-menu__nav-list--level-2 {
                    display: block;
                }
            }

            > .mega-menu__nav-link {
                color: $black;
            }

            .mega-menu__nav-link {
                position: relative;
                padding: 0.5rem 0;

                .svg-icon {/* stylelint-disable-line */
                    position: absolute;
                    top: 10px;
                    left: -23px;
                }
            }
        }
    }

    .mega-menu__nav-list--level-2 {
        position: absolute;
        top: 0;
        left: math.div($max-width, 6);
        display: none;
        width: math.div($max-width, 3);
        height: 100%;
        column-count: 2;
        column-fill: balance;
        column-gap: 0;

        .svg-icon {
            display: none;
        }

        .mega-menu {
            &__nav-item {
                break-inside: avoid;
                padding: 0 0.5rem 0 2rem;
            }

            &__nav-link {
                &:hover,
                &:focus {
                    text-decoration: underline;
                }
            }
        }
    }
}

// Compact menu variation
.primary-nav-wrapper--compact {
    .mega-menu-wrapper {
        width: auto;
        min-width: math.div($max-width, 2);
        margin-top: 15px;

        .mega-menu {
            padding: 0;
        }
    }

    .mega-menu-wrapper--single-teir {
        padding: 0.75rem 2rem;
    }

    .mega-menu-wrapper--double-teir {
        min-width: auto;

        &.mega-menu-wrapper--double-active {
            min-width: math.div($max-width, 2);
        }
    }
}
