/* stylelint-disable */
.feature-branch-manager {
    &__actions {
        display: flex;
    }

    &__reset,
    &__set {
        @include pnp-button;
        margin-right: 1rem;
    }
}

.feature-branch-manager-link-creator {
    width: 500px;
    margin-top: 1rem;
    background: $grey-10;
    padding: 1rem;

    div {
        display: flex;
        justify-content: flex-end;
        margin-top: 1rem;

        &:first-child {
            margin-top: 0;
        }
    }

    label {
        display: inline-block;
        min-width: 100px;
        font-weight: bold;
    }

    input,
    select {
        width: 100%;
    }

    button {
        @include pnp-button;
    }
}

.feature-branch-manager-output {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    width: 500px;
    margin-top: 1rem;
    padding: 1rem;
    background: $grey-10;

    &__link {
        width: 100%;
    }

    &__copy {
        @include pnp-button;
        width: fit-content;
        margin-top: 1rem;
    }
}

.feature-branch-alert {
    padding: 2rem;
    background: $grey-10;
    color: $black;

    &__header {
        @include pnp-max-width-container;
        font-size: 2em;
        text-align: center;
    }

    &__actions {
        @include pnp-max-width-container;
        display: flex;
        justify-content: center;
        margin-top: 1rem;
    }

    &__switcher {
        @include pnp-button;
        margin-right: 1rem;
    }

    &__reset {
        @include pnp-button;
        margin-right: 1rem;
    }
}

.feature-branch-pop-out {
    @include pnp-svg-icon-classes;

    display: flex;
    flex-direction: row-reverse;
    position: fixed;
    top: 100px;
    right: 10px;
    z-index: $feature-branch-picker;

    &--active {
        .feature-branch-pop-out__contents {
            display: flex;
        }
    }

    &__trigger {
        @include pnp-button;
        width: 4rem;
        height: 3rem;
    }

    &__contents {
        left: -500px;
        display: none;
        flex-direction: column;
        margin-right: 1rem;
        border: 2px solid $grey-10;
        background: $white;
        padding: 1rem;
    }

    &__reset,
    &__set {
        @include pnp-button;
        margin-bottom: 1rem;
    }
}
