/* stylelint-disable selector-no-qualifying-type */
/* WYSIWYG STYLING */
@import './content';

html {
    &[data-color-theme='dark'] {
    }

    &[data-color-theme='light'] {
    }
}

body {
    color: $base-font-colour;
    font-family: 'IBM Plex Sans', sans-serif;
}

// Sets black font color, removes backgrounds and borders on printers.
* {
    @include pnp-print-scheme;
}

// Hide all images and image wrappers on printers.
img,
[class*='image-wrapper'] {
    @include pnp-print-hide;
}

.page-layout {
    display: flex;
    flex-direction: column;
    padding: 0 1rem;

    &--full {
        padding: 0;
    }

    &--landing {
        @include pnp-max-width-container();
        margin-top: 2rem;
        margin-bottom: 2rem;
    }

    &--landing-full {
        @include pnp-max-width-container();
        margin-top: 0;
        margin-bottom: 0;
    }

    &--content {
        max-width: $content-max-width;
        margin: auto;
        margin-top: 2rem;
        margin-bottom: 2rem;
    }

    &--content-with-nav {
        @include pnp-max-width-container();
        margin-top: 2rem;
        margin-bottom: 2rem;

        @include pnp-media-breakpoint-up(xl) {
            flex-direction: row-reverse;
        }

        .content {
            max-width: $content-max-width;
        }
    }

    &__body {
        flex-grow: 1;
    }

    &__side {
        flex-shrink: 0;
        @include pnp-media-breakpoint-up(xl) {
            @include pnp-print-hide;

            width: $sidebar-width;
            margin-right: 2rem;
        }
    }
}

.content {
    width: 100%;
}

/*
    Container Template generic classes
*/

.template {
    @each $name, $value in $container-spacing-values {
        &#{'-padding-top--' + $name} {
            padding-top: $value;
        }

        &#{'-padding-bottom--' + $name} {
            padding-bottom: $value;
        }

        &#{'-margin-top--' + $name} {
            margin-top: $value;
        }

        &#{'-margin-bottom--' + $name} {
            margin-bottom: $value;
        }
    }
}
