.related-links {
    @include pnp-svg-icon-classes();
    @include pnp-standard-box-shadow();

    margin: 24px 0;
    padding: 19px;

    &--background {
        @include pnp-generate-background-for-colour-map($pnp-primary-colours);
        @include pnp-generate-background-for-colour-map($pnp-neutral-colours);
    }

    &--color {
        @include pnp-generate-foreground-for-colour-map($pnp-primary-colours);
        @include pnp-generate-foreground-for-colour-map($pnp-neutral-colours);
    }

    &__heading {
        @include pnp-typeface(heading-26-bold, no-margin);
        margin: 3px 0 0;
        font-style: normal;
    }

    &__list {
        @include pnp-list-reset();
    }

    &__link {
        @include pnp-typeface(text-16, no-margin);
        @include pnp-link-text();
        display: inline-block;
        align-items: center;
        padding: 7px 0;
        text-decoration: none;

        &:hover,
        &:focus {
            text-decoration: underline;
        }

        &--color {
            @include pnp-generate-foreground-for-colour-map(
                $pnp-primary-colours
            );
            @include pnp-generate-foreground-for-colour-map(
                $pnp-neutral-colours
            );
        }
    }

    .svg-icon {
        margin-right: 10px;
        vertical-align: middle;
        transform: scale(0.8);
    }
}
